import React from "react";
import "./Panel.css";
import {ShakeLittle} from 'reshake';

const Panel = (props)=>{
    if(props.colors === "red-text"){
        return(
            <div className = "container center  "id = "panel">
                <ShakeLittle>
                    <h5 className = {props.colors}> Click image to earn points,
                     but do not click the same one twice</h5>
                </ShakeLittle>
            </div>
        )
    }else{
        return(
            <div className = "container center  "id = "panel">
                <h5 className = {props.colors}> Click image to earn points,
                 but do not click the same one twice</h5>
            </div>
        )
    }
}

export default Panel



